
.dashboard {
    --outgoing-curve: cubic-bezier(.4, 0, 1, 1);
    --incoming-curve: cubic-bezier(0, 0, .2, 1);
    --incoming-outgoing-curve: cubic-bezier(.4, 0, .2, 1);
    --custom-curve: cubic-bezier(.2, 0, 0, 1);
    --loading-duration: 417ms;
    --loading-animation: fade-in var(--loading-duration) var(--incoming-curve) forwards;
    --default-transition: all .3s linear;
    --default-fading-animation: fade-in .3s linear;
    --common-duration-150: .15s;
    --common-duration-200: .2s;
    --common-duration-250: .25s;
    --common-duration-300: .3s;
    --common-duration-450: .45s;
    --common-duration-500: .5s;
    --ytcp-text-secondary: #5a5a5a;
    --ytcp-line-divider: rgba(255, 255, 255, .1);
    --paper-item-selected-weight: normal;
    --ytcp-general-background-a: #1f1f1f, --ytcp-general-background-a-a0: rgba(31, 31, 31, 0);
    --ytcp-general-background-b: #161616;
    --ytcp-general-background-b-a0: rgba(22, 22, 22, 0);
    --ytcp-general-background-c: #0d0d0d;
    --dashboard_menu_wdt: width .25s ease-in;
}


.dashboard_body {
    --frm-opacity: opacity .25s ease-in, width .25s ease-in;
    -webkit-transition: var(--frm-opacity);
    -o-transition: var(--frm-opacity);
    -moz-transition: var(--frm-opacity);
    transition: var(--frm-opacity);
	width: calc(100vw - var(--menu-slide-wd));
	padding: 0 2rem;	
	overflow-x: hidden;
}


.dashboard_menu {
    -webkit-transition: var(--dashboard_menu_wdt);
    -o-transition: var(--dashboard_menu_wdt);
    -moz-transition: var(--dashboard_menu_wdt);
    transition: var(--dashboard_menu_wdt);
    width: var(--menu-slide-wd);
    padding: .2rem .2rem  .2rem 0 ;	

   
}

.version_box_fixed{
    position: absolute;
    bottom: 0;
    border-top: 1px solid var(--hrm_palette-color-divider_line);
    width: 96%;

}
.version_box_fixed p{
    margin: 5px;
    font-weight: 600;
}
.version_box_fixed svg{
    fill: var(--hrm_palette-float-gray_5A);
}


.dashboard_fixed {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow: hidden;
    width: var(--menu-slide-wd);
    position: fixed;
    min-height: calc(100vh - 2rem);
    border-right: 1px solid var(--hrm_palette-color-divider_line);
}


.dashboard_menu .menu_item {
    color: var(--ytcp-text-secondary);
    padding: 0 20px 0 0;
    cursor: pointer;
    white-space: initial;
    overflow: initial;
    text-overflow: initial;
    font-family: YouTube Sans,Roboto,Arial,sans-serif;
    font-weight: 600;
    font-size: 15px;
    line-height: 24px;
    min-height: 48px;
    background: var(--ytcp-general-background-z);
    margin-right: 4px;
}


.dashboard_menu .menu_item .lbl {
    --menu-labels-expand-transition: opacity var(--common-duration-300) var(--incoming-curve) 117ms;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: var(--menu-labels-expand-transition);
    line-height: 48px;
}



.dashboard_menu .menu_item .icon {
    display: flex;
    flex-direction: row;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    width: var(--paper-item-icon-width, 56px);
    margin-left: var(--paper-item-icon-margin-left, 8);
    margin-right: var(--paper-item-icon-margin-right, 24);
}


.dashboard_menu .menu_item .side_mark {
    margin-right:16px;
    height: 48px;
    width: 4px;
    background-color: var(--ytcp-themed-red);
    background-color: var(--ytcp-text-secondary);
    opacity: var(--ytcp-opacity_line, 0);
    transition: opacity var(--common-duration-150) linear;
}

.dashboard_menu a {
    margin: 0;
}

.cpr_vrs{
    font: 600 11px/24px var(--font_family);margin: 0 7px;
	color: var(--hrm_palette-blue-purple);
}

.email_prf{
    font: 600 13px/24px var(--font_family);margin: 0 7px;
    color: var(--hrm_palette-float-gray_5A);
}

.option_list{
    overflow-y: auto;
    max-height: calc( 100vh - 140px);
}


.print_setting_box{
    padding: 18px 10px
}

.print_setting_box .header{
    font-size: 19px;
    font-weight: 500;
    margin: 4px;
    font-family: "Roboto";
    margin-bottom: 12px;
}

.print_style{
    --color-setting-modal: var(--hrm_palette-color-orange-800);
    --bgcolor-setting-modal: var(--hrm_palette-color-orange-100);
}


.print_style.active{
    --color-setting-modal: var(--hrm_palette-success-fg) ;   
    --bgcolor-setting-modal: var(--hrm_palette-success-100);
}

 .print_style{
     position: relative;
    border  : 1px solid var(--color-setting-modal);
    width: 40%;
    border-radius: 7px;
    padding: 8px;
    color: var(--color-setting-modal);
    background: var(--bgcolor-setting-modal);
}

.print_setting_box .header span{
    color : var(--hrm_palette-inverse-background-b);
}

.print_style svg {
    opacity: 0;
    position: absolute;
    top: 5px;
    left: 5px;
    fill: var(--color-setting-modal);
}
.print_style.active svg {
    opacity: 1;
}

